const c1 = () => {
  const canvas = document.getElementById("c2");
  const ctx = canvas.getContext("2d");

  // –õ—ñ–Ω—ñ—ó
  ctx.beginPath();
  ctx.strokeStyle = "green";
  ctx.lineWidth = "4";
  ctx.moveTo(100, 50);
  ctx.lineTo(150, 150);
  ctx.lineTo(150, 50); // –∑ –æ—Å—Ç–∞–Ω–Ω—å–æ—ó —Ç–æ—á–∫–∏ –º–∞–ª—é—é —ñ–Ω—à—É –¥—ñ—á
  ctx.lineCap = "square"; // –¥–æ–±–∞–≤–ª—è—é—Ç—å—Å—è –∫—Ä–∞–¥—Ä–∞—Ç–∏
  ctx.stroke();

  // —Ä–æ–∑—Ä–∏–≤–∞—é —Å—É—Ü—ñ–ª—å–Ω—É —ñ —Ä–æ–±–ª—é –Ω–æ–≤—É
  ctx.beginPath();
  ctx.strokeStyle = "red";
  ctx.lineWidth = "20";
  ctx.moveTo(150, 50);
  ctx.lineTo(50, 100);
  ctx.lineCap = "butt"; // –ø–æ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—é
  ctx.lineCap = "round"; // –∑–∞–æ–∫—Ä—É–≥–ª—é—é –∫—Ä–∞—ó
  ctx.stroke();

  // —Ä–æ–∑—Ä–∏–≤–∞—é —Å—É—Ü—ñ–ª—å–Ω—É —ñ —Ä–æ–±–ª—é –Ω–æ–≤—É
  ctx.beginPath();
  ctx.strokeStyle = "orange";
  ctx.lineWidth = "16";
  ctx.moveTo(250, 150);
  ctx.lineTo(250, 100);
  ctx.lineTo(200, 100);
  ctx.lineCap = "round"; // –∑–∞–æ–∫—Ä—É–≥–ª—é—é –∫—Ä–∞—ó
  ctx.stroke();

  // —Ç—Ä–∏–∫—É—Ç–Ω–∏–∫
  ctx.beginPath();
  ctx.strokeStyle = "blue";
  ctx.lineWidth = "3";
  ctx.moveTo(300, 150);
  ctx.lineTo(350, 50);
  ctx.lineTo(400, 150);
  // ctx.lineTo(300, 150); // –∞–±–æ –ª—ñ–Ω—ñ—è —è–∫–∞ –ø–æ–≤–µ—Ä—Ç–∞—î —Ç–æ —Å—Ç–∞—Ä—Ç–æ–≤–æ–≥–æ –∞–±–æ –∑–∞–∫—Ä–∏–≤–∞—é, –∫–æ–º–∞–Ω–¥–æ—é –≤–Ω–∏–∑—É
  ctx.closePath();
  ctx.fillStyle = "yellow";
  ctx.stroke();
  ctx.fill();
}; // c2

export default c1;
